import { Link, NavLink } from "react-router-dom";
import { auth } from './firebase'
import { signOut } from 'firebase/auth'
import { useNavigate } from 'react-router-dom'
import useCurrentUser from './Hooks/useCurrentUser'

function Navbar() {
  const navigate = useNavigate()
  const { userData } = useCurrentUser()

  const handleLogout = async () => {
    try {
      await signOut(auth)
      localStorage.removeItem('userData')
      navigate('/login')
    } catch (err) {
      console.error("Logout failed", err)
      alert("Failed to logout.")
    }
  }

  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-light bg-white border-b mb-5">
        <div className="container">
          <Link to="/" className="navbar-brand d-flex align-items-center">
            <div className="bg-primary rounded-lg d-flex align-items-center justify-content-center p-1 mr-2">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                className="lucide lucide-calendar w-5 h-5 text-white">
                <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                <line x1="16" x2="16" y1="2" y2="6"></line>
                <line x1="8" x2="8" y1="2" y2="6"></line>
                <line x1="3" x2="21" y1="10" y2="10"></line>
              </svg>
            </div>
            <span className="font-weight-bold">Campus Engage</span>
          </Link>

          <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse middler" id="navbarSupportedContent">
            <ul className="navbar-nav mx-auto middler">
              <li className="nav-item mr-4">
                <NavLink className="nav-link" to="/"> <i className="fa-regular fa-house mr-1"></i> Home</NavLink>
              </li>
              <li className="nav-item mr-4">
                <NavLink className="nav-link" to="/Events"><i className="fa-light fa-calendar mr-1"></i> Events</NavLink>
              </li>
              
              {/* Show Create Events only for organizers */}
              {userData?.role === "organizer" && (
                <li className="nav-item mr-4">
                  <NavLink className="nav-link" to="/Create"> <i className="fa-regular fa-plus mr-1"></i> Create Events</NavLink>
                </li>
              )}
              
              {/* Show Archived Events only for organizers */}
              {userData?.role === "organizer" && (
                <li className="nav-item">
                  <NavLink className="nav-link" to="/Archived"> <i className="fa-regular fa-box-archive mr-1"></i> Archived Events </NavLink>
                </li>
              )}
            </ul>

            <div className="dropdown ml-md-4">
              <a className="dropdown-toggle d-flex justify-content-center align-items-center bg-primary text-white rounded-circle middler"
                href="#" id="profileDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                aria-expanded="false" style={{ width: 50, height: 50, fontSize: 20 }}>
                {userData?.name ? userData.name.substring(0, 1).toUpperCase() : "U"}
              </a>

              <div className="dropdown-menu profile-menu posit" aria-labelledby="profileDropdown">
                <div className="px-3 py-2 border-bottom">
                  <small className="text-muted d-block">{userData?.name || "User"}</small>
                  <small className="text-muted">{userData?.role || "Role"}</small>
                </div>
                
                <div className="d-flex profile-icons align-items-center">
                  <Link to="/Profile" className="dropdown-item"> <i className="fa-regular fa-user mr-1"></i> Manage My Account</Link>
                </div>

                <div className="d-flex profile-icons align-items-center">
                  <Link to="/Events" className="dropdown-item"> <i className="fa-light fa-calendar mr-1"></i> My Events</Link>
                </div>

                {/* Show organizer-specific options */}
                {userData?.role === "organizer" && (
                  <>
                    <div className="d-flex profile-icons align-items-center">
                      <Link to="/Create" className="dropdown-item"> <i className="fa-regular fa-plus mr-1"></i> Create Event</Link>
                    </div>
                    <div className="d-flex profile-icons align-items-center">
                      <Link to="/Archived" className="dropdown-item"> <i className="fa-regular fa-box-archive mr-1"></i> Archived Events</Link>
                    </div>
                  </>
                )}

                <div className="d-flex profile-icons align-items-center">
                  <button onClick={handleLogout} id="logout-link" className="dropdown-item"> <i className="fa-light fa-right-from-bracket mr-1"></i>
                    Logout</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </div>
  )
}

export default Navbar;