import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { db } from "../firebase";
import { collection, getDocs, query, where } from "firebase/firestore";
import useCurrentUser from "../Hooks/useCurrentUser";

function Allhome() {
  const { userData } = useCurrentUser();
  const [eventsCreated, setEventsCreated] = useState(0);
  const [totalRegistrations, setTotalRegistrations] = useState(0);
  const [attendancesChecked, setAttendancesChecked] = useState(0);
  const [myEvents, setMyEvents] = useState([]);
  const [upcomingEvents, setUpcomingEvents] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      if (!userData) return;

      try {
        // Fetch events created by the current user (organizer)
        if (userData.role === "organizer") {
          const createdEventsQuery = query(
            collection(db, "events"),
            where("organizerId", "==", userData.id)
          );
          const createdEventsSnapshot = await getDocs(createdEventsQuery);
          const events = createdEventsSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
          setEventsCreated(createdEventsSnapshot.size);
          setMyEvents(events);

          // Fetch total registrations for events created by the current user
          let totalReg = 0;
          let totalAttended = 0;
          
          for (const event of events) {
            const registrationsQuery = query(
              collection(db, "registrations"),
              where("eventId", "==", event.id)
            );
            const registrationsSnapshot = await getDocs(registrationsQuery);
            totalReg += registrationsSnapshot.size;

            const attendedQuery = query(
              collection(db, "registrations"),
              where("eventId", "==", event.id),
              where("attended", "==", true)
            );
            const attendedSnapshot = await getDocs(attendedQuery);
            totalAttended += attendedSnapshot.size;
          }
          
          setTotalRegistrations(totalReg);
          setAttendancesChecked(totalAttended);
        } else if (userData.role === "student") {
          // Fetch events registered by the current student
          const registeredEventsQuery = query(
            collection(db, "registrations"),
            where("userId", "==", userData.id)
          );
          const registeredEventsSnapshot = await getDocs(registeredEventsQuery);
          setTotalRegistrations(registeredEventsSnapshot.size);

          // Fetch events attended by the current student
          const attendedEventsQuery = query(
            collection(db, "registrations"),
            where("userId", "==", userData.id),
            where("attended", "==", true)
          );
          const attendedEventsSnapshot = await getDocs(attendedEventsQuery);
          setAttendancesChecked(attendedEventsSnapshot.size);
        }

        // Fetch upcoming events (for all users)
        const today = new Date();
        const todayString = today.toISOString().split("T")[0];
        
        const allEventsQuery = collection(db, "events");
        const allEventsSnapshot = await getDocs(allEventsQuery);
        const allEvents = allEventsSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        
        // Filter upcoming events (events that are today or in the future)
        const upcoming = allEvents.filter(event => {
          const eventDate = new Date(event.date);
          const today = new Date();
          today.setHours(0, 0, 0, 0); // Reset time to start of day for comparison
          return eventDate >= today;
        });
        
        setUpcomingEvents(upcoming);
      } catch (error) {
        console.error("Error fetching data:", error);
      }
    };

    fetchData();
  }, [userData]); // Removed myEvents from dependency to prevent infinite loop

  return (
    <div className="container flex-fill">
      <div className="row">
        <div className="col-md-12">
          <div className="bg-primary bordersm px-3 py-4 d-flex align-items-center justify-content-between mb-4">
            <div>
              <h3 className="text-white">Welcome Back, {userData?.name || "Guest"}!</h3>
              <span className="text-white">Manage your events, engage with the campus and learn!</span>
            </div>
            {userData?.role === "organizer" && (
              <div>
                <Link to="/Create" className="btn bg-white rounded-lg font-weight-bold">
                  <i className="fa-regular fa-plus mr-1"></i> Create Events
                </Link>
              </div>
            )}
          </div>
        </div>

        <div className="col-md-4 phone-mg">
          <div className="d-flex bordersm justify-content-between bg-white border shadow-sm p-4">
            <div>
              <span className="text-muted">Events Created</span>
              <h2>{eventsCreated}</h2>
            </div>
            <Link to="/MyEvents"
              className="bg-primary fit rounded-lg d-flex align-items-center justify-content-center p-2">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                className="lucide lucide-calendar w-5 h-5 text-white">
                <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                <line x1="16" x2="16" y1="2" y2="6"></line>
                <line x1="8" x2="8" y1="2" y2="6"></line>
                <line x1="3" x2="21" y1="10" y2="10"></line>
              </svg>
            </Link>
          </div>
        </div>

        <div className="col-md-4 phone-mg">
          <div className="d-flex bordersm justify-content-between bg-white border shadow-sm p-4">
            <div>
              <span className="text-muted">Total Registrations</span>
              <h2>{totalRegistrations}</h2>
            </div>
            <Link to="/MyEvents"
              className="bg-danger fit rounded-lg d-flex align-items-center justify-content-center p-2">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                className="lucide lucide-star w-6 h-6 text-white">
                <polygon
                  points="12 2 15.09 8.26 22 9.27 17 14.14 18.18 21.02 12 17.77 5.82 21.02 7 14.14 2 9.27 8.91 8.26 12 2">
                </polygon>
              </svg>
            </Link>
          </div>
        </div>

        <div className="col-md-4 mb-4">
          <div className="d-flex bordersm justify-content-between bg-white border shadow-sm p-4">
            <div>
              <span className="text-muted">Attendances Checked</span>
              <h2>{attendancesChecked}</h2>
            </div>
            <Link to="/MyEvents"
              className="bg-success fit rounded-lg d-flex align-items-center justify-content-center p-2">
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                className="lucide lucide-users w-6 h-6 text-white">
                <path d="M16 21v-2a4 4 0 0 0-4-4H6a4 4 0 0 0-4 4v2"></path>
                <circle cx="9" cy="7" r="4"></circle>
                <path d="M22 21v-2a4 4 0 0 0-3-3.87"></path>
                <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
              </svg>
            </Link>
          </div>
        </div>

        <div className="col-md-12 mb-4">
          <div className="bg-white bordersm p-4 shadow-sm border">
            <div className="d-flex align-items-center justify-content-between mb-4">
              <h2 className="h5 font-weight-bold text-dark mb-0">My Events</h2>
              {userData?.role === "organizer" && (
                <Link to="/Create"> <i className="fa-regular fa-plus mr-1"></i> Create Events</Link>
              )}
            </div>
            {myEvents.length === 0 ? (
              <p>No events created yet.</p>
            ) : (
              myEvents.map((event) => (
                <div key={event.id} className="mb-3 border py-4 px-3 bordersm">
                  <div className="d-flex justify-content-between">
                    <div>
                      <h3 className="h6 font-weight-bold text-dark mb-2">{event.title}</h3>
                      <div className="d-flex text-muted small mb-2">
                        <div className="d-flex align-items-center mr-3">
                          <i className="fa-light fa-calendar mr-1"></i>
                          <span>{event.date}</span>
                        </div>
                        <div className="d-flex align-items-center mr-3">
                          <i className="fa-regular fa-clock mr-1"></i>
                          <span>{event.time}</span>
                        </div>
                        <div className="d-flex align-items-center">
                          <i className="fa-light fa-users mr-1"></i>
                          <span>{event.registeredCount || 0}/{event.maxAttendees}</span>
                        </div>
                      </div>
                      <div className="d-flex align-items-center">
                        {event.tags && event.tags.map((tag, index) => (
                          <span key={index} className="d-inline-block px-2 py-1 small bg-light text-secondary rounded mr-1">#{tag}</span>
                        ))}
                      </div>
                    </div>
                    <div>
                      <Link to={`/Details/${event.id}`} className="text-muted">
                        <i className="fa-regular fa-pen-to-square"></i>
                      </Link>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>

        <div className="col-md-12">
          <div className="bg-white bordersm p-4 shadow-sm border">
            <div className="d-flex align-items-center justify-content-between mb-4">
              <h2 className="h5 font-weight-bold text-dark mb-0">Upcoming Events</h2>
              <Link to="/Events">View All</Link>
            </div>
            {upcomingEvents.length === 0 ? (
              <p>No upcoming events.</p>
            ) : (
              upcomingEvents.map((event) => (
                <div key={event.id} className="mb-3 border py-4 px-3 bordersm">
                  <div className="d-flex justify-content-between">
                    <div>
                      <div className="d-flex align-items-center mb-2">
                        <h3 className="h6 font-weight-bold text-dark mb-0 mr-3">{event.title}</h3>
                        <span className="small text-primary">{event.category}</span>
                      </div>
                      <div className="d-flex text-muted small mb-2">
                        <div className="d-flex align-items-center mr-3">
                          <i className="fa-light fa-calendar mr-1"></i>
                          <span>{event.date}</span>
                        </div>
                        <div className="d-flex align-items-center mr-3">
                          <i className="fa-regular fa-clock mr-1"></i>
                          <span>{event.time}</span>
                        </div>
                        <div className="d-flex align-items-center">
                          <i className="fa-regular fa-location-dot mr-1"></i>
                          <span>{event.location}</span>
                        </div>
                      </div>
                      <div className="d-flex align-items-center">
                        {event.tags && event.tags.map((tag, index) => (
                          <span key={index} className="d-inline-block px-2 py-1 small bg-light text-secondary rounded mr-1">#{tag}</span>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Allhome;

