import React, { useState, useEffect } from 'react';
import { Link, useParams } from 'react-router-dom'
import { db } from '../firebase'
import { doc, getDoc, collection, query, where, getDocs, addDoc, deleteDoc, updateDoc, increment } from 'firebase/firestore'
import useCurrentUser from '../Hooks/useCurrentUser';
import QRCodeGenerator from '../QRCode/QRCodeGenerator'
import QRScanner from '../QRCode/QRScanner'

function Details() {
  const { eventId } = useParams();
  const { userData } = useCurrentUser();
  const [event, setEvent] = useState(null);
  const [attendees, setAttendees] = useState([]);
  const [loading, setLoading] = useState(true);
  const [isRegistered, setIsRegistered] = useState(false);
  const [registrationId, setRegistrationId] = useState(null);
  const [showScanner, setShowScanner] = useState(false);

  const fetchEventData = async () => {
    try {
      // Fetch event details
      const eventDoc = await getDoc(doc(db, "events", eventId));
      if (eventDoc.exists()) {
        setEvent({ id: eventDoc.id, ...eventDoc.data() });
      }

      // Fetch attendees (assuming there's a registrations collection)
      const attendeesQuery = query(
        collection(db, "registrations"),
        where("eventId", "==", eventId)
      );
      const attendeesSnapshot = await getDocs(attendeesQuery);
      const attendeesData = attendeesSnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setAttendees(attendeesData);

      // Check if current user is registered
      if (userData) {
        const userRegistration = attendeesData.find(attendee => attendee.userId === userData.id);
        if (userRegistration) {
          setIsRegistered(true);
          setRegistrationId(userRegistration.id);
        }
      }
    } catch (error) {
      console.error("Error fetching event data:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleRegister = async () => {
    if (!userData) {
      alert('Please log in to register for events.');
      return;
    }

    if (event.registeredCount >= event.maxAttendees) {
      alert('This event is full. Registration is not available.');
      return;
    }

    try {
      const registrationData = {
        eventId: eventId,
        userId: userData.id,
        userName: userData.name,
        userEmail: userData.email,
        studentId: userData.studentId,
        registrationDate: new Date().toISOString(),
        attended: false
      };

      const docRef = await addDoc(collection(db, 'registrations'), registrationData);
      await updateDoc(doc(db, 'events', eventId), {
        registeredCount: increment(1)
      });

      setIsRegistered(true);
      setRegistrationId(docRef.id);
      alert(' Successfully registered for the event!');
      fetchEventData(); // Refresh data
    } catch (error) {
      console.error('Error registering for event:', error);
      alert(' Failed to register for the event.');
    }
  };

  const handleUnregister = async () => {
    if (!registrationId) return;

    if (window.confirm('Are you sure you want to unregister from this event?')) {
      try {
        await deleteDoc(doc(db, 'registrations', registrationId));
        await updateDoc(doc(db, 'events', eventId), {
          registeredCount: increment(-1)
        });

        setIsRegistered(false);
        setRegistrationId(null);
        alert(' Successfully unregistered from the event.');
        fetchEventData(); // Refresh data
      } catch (error) {
        console.error('Error unregistering from event:', error);
        alert(' Failed to unregister from the event.');
      }
    }
  };

  const handleQRScan = async (scannedData) => {
    try {
      const data = JSON.parse(scannedData);
      console.log('Scanned QR data:', data);
      
      if (data.userId && data.eventId) {
        // Find the registration for this user and event
        const registrationQuery = query(
          collection(db, 'registrations'),
          where('userId', '==', data.userId),
          where('eventId', '==', data.eventId)
        );
        const registrationSnapshot = await getDocs(registrationQuery);
        
        if (!registrationSnapshot.empty) {
          const registrationDoc = registrationSnapshot.docs[0];
          await updateDoc(registrationDoc.ref, { attended: true });
          alert(' Attendance marked successfully!');
          fetchEventData(); // Refresh data
        } else {
          alert(' Registration not found for this user and event.');
        }
      } else {
        alert(' Invalid QR code data.');
      }
    } catch (error) {
      console.error('Error processing QR scan:', error);
      alert(' Failed to process QR code: ' + error.message);
    }
  };

  const handleShare = async () => {
    const shareData = {
      title: event.title,
      text: `Check out this event: ${event.title}`,
      url: window.location.href
    };

    try {
      if (navigator.share && navigator.canShare && navigator.canShare(shareData)) {
        await navigator.share(shareData);
      } else {
        // Fallback: copy to clipboard
        await navigator.clipboard.writeText(window.location.href);
        alert(' Event link copied to clipboard!');
      }
    } catch (error) {
      console.error('Error sharing:', error);
      // Final fallback: manual copy
      const textArea = document.createElement('textarea');
      textArea.value = window.location.href;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      alert(' Event link copied to clipboard!');
    }
  };

  useEffect(() => {
    if (eventId) {
      fetchEventData();
    }
  }, [eventId, userData]);

  if (loading) {
    return (
      <main className="event-container">
        <div className="event-wrapper">
          <div className="event-content">
            <div className="text-center py-5">
              <h3>Loading event details...</h3>
            </div>
          </div>
        </div>
      </main>
    );
  }

  if (!event) {
    return (
      <main className="event-container">
        <div className="event-wrapper">
          <div className="event-content">
            <div className="text-center py-5">
              <h3>Event not found</h3>
              <Link to="/Events" className="btn btn-primary mt-3">Back to Events</Link>
            </div>
          </div>
        </div>
      </main>
    );
  }

  const registrationRate = event.maxAttendees > 0 ? Math.round((event.registeredCount / event.maxAttendees) * 100) : 0;
  const attendedCount = attendees.filter(attendee => attendee.attended).length;
  return (
    <main className="event-container flex-fill">
        <div className="event-wrapper">
            <div className="event-content">
                
                {/* <!-- Header --> */}
                <div className="event-header">
                    <Link to="/Events" className="event-back-btn text-dark">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none"
                            stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round">
                            <path d="m12 19-7-7 7-7"></path>
                            <path d="M19 12H5"></path>
                        </svg>
                    </Link>
                    <h1 className="event-title">Event Details</h1>
                </div>

                <div className="event-layout">
                    
                    {/* <!-- Main Content --> */}
                    <div className="event-main">
                        
                        {/* <!-- Event Card --> */}
                        <div className="event-card">
                            <div className="event-card-body">
                                
                                {/* <!-- Header with badge and actions --> */}
                                <div className="event-card-header">
                                    <span className="event-badge event-badge-primary">{event.category || "Uncategorized"}</span>
                                    <div className="event-actions">
                                        <button className="event-action-btn" title="Share event" onClick={handleShare}>
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round">
                                                <circle cx="18" cy="5" r="3"></circle>
                                                <circle cx="6" cy="12" r="3"></circle>
                                                <circle cx="18" cy="19" r="3"></circle>
                                                <line x1="8.59" x2="15.42" y1="13.51" y2="17.49"></line>
                                                <line x1="15.41" x2="8.59" y1="6.51" y2="10.49"></line>
                                            </svg>
                                        </button>
                                        <button className="event-action-btn" title="Edit event">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round">
                                                <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7">
                                                </path>
                                                <path d="M18.5 2.5a2.12 2.12 0 0 1 3 3L12 15l-4 1 1-4Z"></path>
                                            </svg>
                                        </button>
                                        <button className="event-action-btn event-action-btn-danger" title="Delete event">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round">
                                                <path d="M3 6h18"></path>
                                                <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"></path>
                                                <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"></path>
                                                <line x1="10" x2="10" y1="11" y2="17"></line>
                                                <line x1="14" x2="14" y1="11" y2="17"></line>
                                            </svg>
                                        </button>
                                    </div>
                                </div>

                                
                                {/* <!-- Event Title --> */}
                                <h1 className="event-name">{event.title}</h1>

                                
                                {/* <!-- Tags --> */}
                                <div className="event-tags">
                                    {(event.tags || []).map((tag, i) => (
                                        <span key={i} className="event-tag mb-1">#{tag}</span>
                                    ))}
                                </div>

                                
                                {/* <!-- Event Details Grid --> */}
                                <div className="event-details-grid">
                                    <div className="event-detail-item">
                                        <div className="event-detail-content">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round"
                                                className="event-detail-icon">
                                                <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                                                <line x1="16" x2="16" y1="2" y2="6"></line>
                                                <line x1="8" x2="8" y1="2" y2="6"></line>
                                                <line x1="3" x2="21" y1="10" y2="10"></line>
                                            </svg>
                                            <span className="event-detail-text">{event.date}</span>
                                        </div>
                                    </div>
                                    <div className="event-detail-item">
                                        <div className="event-detail-content">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round"
                                                className="event-detail-icon">
                                                <circle cx="12" cy="12" r="10"></circle>
                                                <polyline points="12 6 12 12 16 14"></polyline>
                                            </svg>
                                            <span className="event-detail-text">{event.startTime} - {event.endTime}</span>
                                        </div>
                                    </div>
                                    <div className="event-detail-item">
                                        <div className="event-detail-content">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round"
                                                className="event-detail-icon">
                                                <path d="M20 10c0 6-8 12-8 12s-8-6-8-12a8 8 0 0 1 16 0Z"></path>
                                                <circle cx="12" cy="10" r="3"></circle>
                                            </svg>
                                            <span className="event-detail-text">{event.location}</span>
                                        </div>
                                    </div>
                                    <div className="event-detail-item">
                                        <div className="event-detail-content">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18"
                                                viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2"
                                                stroke-linecap="round" stroke-linejoin="round"
                                                className="event-detail-icon">
                                                <path d="M16 21v-2a4 4 0 0 0-4-4H6a4 4 0 0 0-4 4v2"></path>
                                                <circle cx="9" cy="7" r="4"></circle>
                                                <path d="M22 21v-2a4 4 0 0 0-3-3.87"></path>
                                                <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
                                            </svg>
                                            <span className="event-detail-text">{event.registeredCount}/{event.maxAttendees} registered</span>
                                        </div>
                                    </div>
                                </div>

                                
                                {/* <!-- Description Section --> */}
                                <div className="event-section">
                                    <h2 className="event-section-title">Description</h2>
                                    <p className="event-section-text">{event.description}</p>
                                </div>

                                
                                {/* <!-- Organizer Section --> */}
                                <div className="event-section">
                                    <h2 className="event-section-title">Organizer</h2>
                                    <p className="event-section-text">{event.organizer || "Event Organizer"}</p>
                                </div>
                            </div>
                        </div>

                        
                        {/* <!-- Attendees Card --> */}
                        <div className="event-card">
                            <div className="event-card-body">
                                <h2 className="event-attendees-title">Attendees ({attendees.length})</h2>
                                {attendees.length > 0 ? (
                                    attendees.map((attendee, index) => (
                                        <div key={attendee.id} className="event-attendee-item mb-3">
                                            <div className="event-attendee-card">
                                                <div className="event-attendee-avatar">
                                                    <div className="event-avatar-circle">
                                                        {(attendee.userName || attendee.name || "U").charAt(0).toUpperCase()}
                                                    </div>
                                                </div>
                                                <div className="event-attendee-info">
                                                    <div className="event-attendee-header">
                                                        <h3 className="event-attendee-name">{attendee.userName || attendee.name || "Anonymous"}</h3>
                                                        <span className={`event-badge ${attendee.attended ? 'event-badge-success' : 'event-badge-info'}`}>
                                                            {attendee.attended ? 'Attended' : 'Registered'}
                                                        </span>
                                                    </div>
                                                    <div className="event-attendee-details">
                                                        <div className="event-attendee-id">Student ID: {attendee.studentId || "N/A"}</div>
                                                        <div className="event-attendee-date">Registered: {attendee.registrationDate || "N/A"}</div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                ) : (
                                    <p className="text-muted">No attendees registered yet.</p>
                                )}
                            </div>
                        </div>
                    </div>

                    
                    {/* <!-- Sidebar --> */}
                    <div className="event-sidebar">
                        
                        {/* <!-- Registration Card --> */}
                        <div className="event-card">
                            <div className="event-card-body">
                                <h3 className="event-sidebar-title">Registration</h3>
                                <div className="event-registration-info">
                                    <div className="event-spots-info">
                                        <span className="event-spots-label">Available spots:</span>
                                        <span className="event-spots-value">{event.maxAttendees - event.registeredCount}</span>
                                    </div>
                                    <div className="event-progress-bar">
                                        <div className="event-progress-fill" style={{width: `${registrationRate}%`}}></div>
                                    </div>
                                </div>
                                
                                {isRegistered ? (
                                    <div>
                                        <div className="alert alert-success mb-3">
                                            <strong> You are registered for this event!</strong>
                                        </div>
                                        <div className="mb-3">
                                            <QRCodeGenerator 
                                                eventId={eventId}
                                                userId={userData?.id}
                                                userName={userData?.name}
                                                registrationId={registrationId}
                                            />
                                        </div>
                                        <button onClick={handleUnregister} className="event-btn event-btn-danger w-100">Unregister</button>
                                    </div>
                                ) : (
                                    <button onClick={handleRegister} className="event-btn event-btn-primary">Register for Event</button>
                                )}
                            </div>
                        </div>

                        
                        {/* <!-- QR Scanner Card --> */}
                        {userData?.role === 'organizer' && (
                            <div className="event-card">
                                <div className="event-card-body">
                                    <h3 className="event-sidebar-title event-scanner-title">
                                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24"
                                            fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round"
                                            strokeLinejoin="round" className="event-scanner-icon">
                                            <path d="M3 7V5a2 2 0 0 1 2-2h2"></path>
                                            <path d="M17 3h2a2 2 0 0 1 2 2v2"></path>
                                            <path d="M21 17v2a2 2 0 0 1-2 2h-2"></path>
                                            <path d="M7 21H5a2 2 0 0 1-2-2v-2"></path>
                                        </svg>
                                        Attendance Scanner
                                    </h3>
                                    <div className="event-scanner-content">
                                        {!showScanner ? (
                                            <div>
                                                <p className="event-scanner-text">Scan attendee QR codes to mark attendance</p>
                                                <button 
                                                    className="event-btn event-btn-success"
                                                    onClick={() => setShowScanner(true)}
                                                >
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18"
                                                        viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2"
                                                        strokeLinecap="round" strokeLinejoin="round" className="event-btn-icon">
                                                        <path d="M3 7V5a2 2 0 0 1 2-2h2"></path>
                                                        <path d="M17 3h2a2 2 0 0 1 2 2v2"></path>
                                                        <path d="M21 17v2a2 2 0 0 1-2 2h-2"></path>
                                                        <path d="M7 21H5a2 2 0 0 1-2-2v-2"></path>
                                                    </svg>
                                                    Start QR Scanner
                                                </button>
                                            </div>
                                        ) : (
                                            <div>
                                                <QRScanner 
                                                    onScan={handleQRScan}
                                                />
                                                <button 
                                                    className="btn btn-secondary mt-2 w-100"
                                                    onClick={() => setShowScanner(false)}
                                                >
                                                    Close Scanner
                                                </button>
                                            </div>
                                        )}
                                    </div>
                                </div>
                            </div>
                        )}

                        
                        {/* <!-- Event Stats Card --> */}
                        <div className="event-card">
                            <div className="event-card-body">
                                <h3 className="event-sidebar-title">Event Stats</h3>
                                <div className="event-stats-list">
                                    <div className="event-stat-item">
                                        <span className="event-stat-label">Total Capacity:</span>
                                        <span className="event-stat-value">{event.maxAttendees}</span>
                                    </div>
                                    <div className="event-stat-item">
                                        <span className="event-stat-label">Registered:</span>
                                        <span className="event-stat-value">{event.registeredCount}</span>
                                    </div>
                                    <div className="event-stat-item">
                                        <span className="event-stat-label">Attended:</span>
                                        <span className="event-stat-value">{attendedCount}</span>
                                    </div>
                                    <div className="event-stat-item">
                                        <span className="event-stat-label">Registration Rate:</span>
                                        <span className="event-stat-value">{registrationRate}%</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </main>
  )
}

export default Details