import React, { useState } from "react"
import { auth, db } from "../firebase"
import { createUserWithEmailAndPassword } from "firebase/auth"
import { doc, setDoc } from "firebase/firestore"
import { useNavigate, Link } from "react-router-dom"

function Singup() {
    const [role, setRole] = useState("student")
    const [name, setName] = useState("")
    const [userId, setUserId] = useState("");
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [confirmPassword, setConfirmPassword] = useState("")
    const [bio, setBio] = useState("")
    const [loading, setLoading] = useState(false)
    const navigate = useNavigate()

    const handleSubmit = async (e) => {
        e.preventDefault()
        setLoading(true)

        if (password !== confirmPassword) {
            setLoading(false)
            return alert(" Passwords do not match")
        }

        if (!email.endsWith("@poornima.edu")) {
            setLoading(false)
            return alert(" Only @poornima.edu email addresses are allowed")
        }

        try {
            // Create Firebase Auth user
            const userCred = await createUserWithEmailAndPassword(auth, email, password)
            const uid = userCred.user.uid

            // Store additional info in Firestore
            const userData = {
                name,
                role,
                studentId: userId,
                email,
                bio,
                createdAt: new Date()
            }
            
            await setDoc(doc(db, "users", uid), userData)

            // Store user data in localStorage
            localStorage.setItem('userData', JSON.stringify({ id: uid, ...userData }))

            alert(" Signup complete!")
            navigate("/") // Navigate to home
        } catch (err) {
            console.error(err)
            alert(" Signup failed: " + err.message)
        } finally {
            setLoading(false)
        }
    }
    return (
        <div className="container mt-5 mb-5">
            <div className="row">
                <div className="regbord col-md-6 bg-primary text-white p-4 p-lg-5">
                    <div className="d-flex align-items-center mb-4">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"
                            stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
                            className="lucide lucide-calendar w-5 h-5 text-white">
                            <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                            <line x1="16" x2="16" y1="2" y2="6"></line>
                            <line x1="8" x2="8" y1="2" y2="6"></line>
                            <line x1="3" x2="21" y1="10" y2="10"></line>
                        </svg>
                        <h1 className="h4 font-weight-bold mb-0 ml-2">Campus Engage</h1>
                    </div>

                    <h2 className="h3 font-weight-bold mb-3">Connect. Engage. Participate.</h2>
                    <p className="text-light mb-4">
                        The ultimate platform for campus life - bringing students and events together through smart
                        technology.
                    </p>

                    <div>

                        {/* <!-- Item 1 --> */}
                        <div className="d-flex mb-4">
                            <div className="bg-primary rounded p-2 mr-3">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"
                                    fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
                                    stroke-linejoin="round" className="lucide lucide-calendar w-5 h-5">
                                    <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                                    <line x1="16" x2="16" y1="2" y2="6"></line>
                                    <line x1="8" x2="8" y1="2" y2="6"></line>
                                    <line x1="3" x2="21" y1="10" y2="10"></line>
                                </svg>
                            </div>
                            <div>
                                <h5 className="font-weight-semibold mb-1">Centralized Events</h5>
                                <p className="text-light small mb-0">All campus events in one place - no more scattered
                                    information</p>
                            </div>
                        </div>


                        {/* <!-- Item 2 --> */}
                        <div className="d-flex mb-4">
                            <div className="bg-primary rounded p-2 mr-3">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"
                                    fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
                                    stroke-linejoin="round" className="lucide lucide-bar-chart3 w-5 h-5">
                                    <path d="M3 3v18h18"></path>
                                    <path d="M18 17V9"></path>
                                    <path d="M13 17V5"></path>
                                    <path d="M8 17v-3"></path>
                                </svg>
                            </div>
                            <div>
                                <h5 className="font-weight-semibold mb-1">Event Analytics</h5>
                                <p className="text-light small mb-0">Track attendance and engagement statistics</p>
                            </div>
                        </div>


                        {/* <!-- Item 3 --> */}
                        <div className="d-flex mb-4">
                            <div className="bg-primary rounded p-2 mr-3">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"
                                    fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
                                    stroke-linejoin="round" className="lucide lucide-users w-5 h-5">
                                    <path d="M16 21v-2a4 4 0 0 0-4-4H6a4 4 0 0 0-4 4v2"></path>
                                    <circle cx="9" cy="7" r="4"></circle>
                                    <path d="M22 21v-2a4 4 0 0 0-3-3.87"></path>
                                    <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
                                </svg>
                            </div>
                            <div>
                                <h5 className="font-weight-semibold mb-1">Easy Registration</h5>
                                <p className="text-light small mb-0">Register for events with just one click</p>
                            </div>
                        </div>


                        {/* <!-- Item 4 --> */}
                        <div className="d-flex mb-4">
                            <div className="bg-primary rounded p-2 mr-3">
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"
                                    fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
                                    stroke-linejoin="round" className="lucide lucide-bar-chart3 w-5 h-5">
                                    <path d="M3 3v18h18"></path>
                                    <path d="M18 17V9"></path>
                                    <path d="M13 17V5"></path>
                                    <path d="M8 17v-3"></path>
                                </svg>
                            </div>
                            <div>
                                <h5 className="font-weight-semibold mb-1">Attendance Tracking</h5>
                                <p className="text-light small mb-0">QR code check-ins and automated attendance sheets</p>
                            </div>
                        </div>
                    </div>
                </div>


                <div className="regbord2 col-md-6 bg-white d-flex flex-column justify-content-center shadow-sm p-5">
                    <h2 className="mb-1 font-weight-bold">Create Account</h2>
                    <p className="mb-4 mt-2 text-muted">Join the campus and enjoy our events!</p>

                    <form onSubmit={handleSubmit} className="signup-form">

                        <div>
                            <label className="text-muted font-weight-light">Account type *</label>
                            <select
                                className="form-control mb-3"
                                value={role}
                                onChange={(e) => setRole(e.target.value)}
                            >
                                <option value="student">Student</option>
                                <option value="organizer">Organizer</option>
                            </select>
                        </div>

                        <div>
                            <label className="text-muted font-weight-light">Full Name *</label>
                            <input
                                type="text"
                                className="form-control py-4 rounded-lg"
                                placeholder="Name"
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                required
                            />
                        </div>

                        <div className="my-4">
                            <label className="text-muted font-weight-light">
                                {role === "organizer" ? "Department / Organization *" : "Student ID *"}
                            </label>
                            <input
                                type="text"
                                className="form-control py-4"
                                placeholder={role === "organizer" ? "CS Department / IEEE Club..." : "ST123..."}
                                value={userId}
                                onChange={(e) => setUserId(e.target.value)}
                                required
                            />
                        </div>

                        <div className="mb-4">
                            <label className="text-muted font-weight-light">College Email *</label>
                            <input
                                type="email"
                                className="form-control py-4"
                                placeholder="student@university.edu"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                            />
                        </div>

                        <div className="mb-4">
                            <label className="text-muted font-weight-light">Password *</label>
                            <input
                                type="password"
                                className="form-control py-4"
                                placeholder="****"
                                value={password}
                                onChange={(e) => setPassword(e.target.value)}
                                required
                            />
                        </div>

                        <div className="mb-4">
                            <label className="text-muted font-weight-light">Confirm Password *</label>
                            <input
                                type="password"
                                className="form-control py-4"
                                placeholder="****"
                                value={confirmPassword}
                                onChange={(e) => setConfirmPassword(e.target.value)}
                                required
                            />
                        </div>

                        <div className="mb-4">
                            <label className="text-muted font-weight-light">Bio (Optional)</label>
                            <textarea
                                className="form-control"
                                rows="3"
                                placeholder="Tell us about yourself..."
                                value={bio}
                                onChange={(e) => setBio(e.target.value)}
                            />
                        </div>

                        <button type="submit" className="btn btn-primary w-100" disabled={loading}>
                            {loading ? "Creating Account..." : "Sign Up"}
                        </button>
                    </form>

                    <div id="result"></div>

                    <div className="d-flex flex-column justify-content-center">
                        <span className="text-center text-muted">Already have account? <Link to="/login"
                            className="already-have-account">Log in</Link> </span>
                    </div>

                </div>

            </div>
        </div>
    )
}

export default Singup