import { Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { db } from "../firebase";
import { collection, getDocs, query, where, addDoc, deleteDoc, doc, updateDoc, increment } from "firebase/firestore";
import useCurrentUser from "../Hooks/useCurrentUser";

function Events() {
  const { userData } = useCurrentUser();
  const [events, setEvents] = useState([]);
  const [filteredEvents, setFilteredEvents] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedTag, setSelectedTag] = useState('');
  const [allTags, setAllTags] = useState([]);
  const [userRegistrations, setUserRegistrations] = useState({});

  useEffect(() => {
    const fetchEvents = async () => {
      try {
        // First, check and archive events that have passed their end time
        await archiveExpiredEvents();
        
        // Fetch only non-archived events
        const querySnapshot = await getDocs(collection(db, "events"));
        const allEventData = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }));
        
        // Filter out archived events and events that have ended
        const currentTime = new Date();
        const activeEvents = allEventData.filter(event => {
          if (event.archived) return false;
          
          // Check if event has ended
          const eventDate = new Date(event.date);
          const [endHour, endMinute] = event.endTime.split(':');
          eventDate.setHours(parseInt(endHour), parseInt(endMinute), 0, 0);
          
          return eventDate > currentTime;
        });
        
        setEvents(activeEvents);
        setFilteredEvents(activeEvents);

        // Extract all unique tags
        const tags = new Set();
        activeEvents.forEach(event => {
          if (event.tags && Array.isArray(event.tags)) {
            event.tags.forEach(tag => tags.add(tag));
          }
        });
        setAllTags(Array.from(tags));
      } catch (error) {
        console.error("Error fetching events:", error);
      }
    };

    const archiveExpiredEvents = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "events"));
        const currentTime = new Date();
        
        for (const docSnapshot of querySnapshot.docs) {
          const event = docSnapshot.data();
          
          // Skip if already archived
          if (event.archived) continue;
          
          // Check if event has ended
          const eventDate = new Date(event.date);
          const [endHour, endMinute] = event.endTime.split(':');
          eventDate.setHours(parseInt(endHour), parseInt(endMinute), 0, 0);
          
          if (eventDate <= currentTime) {
            // Archive the event
            await updateDoc(doc(db, "events", docSnapshot.id), {
              archived: true,
              archiveDate: new Date().toISOString().split('T')[0]
            });
            console.log(`Archived event: ${event.title}`);
          }
        }
      } catch (error) {
        console.error("Error archiving expired events:", error);
      }
    };

    const fetchUserRegistrations = async () => {
      if (!userData?.id) return;
      
      try {
        const registrationsQuery = query(
          collection(db, "registrations"),
          where("userId", "==", userData.id)
        );
        const registrationsSnapshot = await getDocs(registrationsQuery);
        const registrations = {};
        registrationsSnapshot.docs.forEach(doc => {
          const data = doc.data();
          registrations[data.eventId] = doc.id;
        });
        setUserRegistrations(registrations);
      } catch (error) {
        console.error("Error fetching user registrations:", error);
      }
    };

    fetchEvents();
    fetchUserRegistrations();
  }, [userData]);

  useEffect(() => {
    // Filter events based on search term and selected tag
    let filtered = events;

    // Filter by search term (title, description, location)
    if (searchTerm.trim()) {
      filtered = filtered.filter(event =>
        event.title?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        event.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
        event.location?.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    // Filter by selected tag
    if (selectedTag) {
      filtered = filtered.filter(event =>
        event.tags && event.tags.includes(selectedTag)
      );
    }

    setFilteredEvents(filtered);
  }, [events, searchTerm, selectedTag]);

  const clearFilters = () => {
    setSearchTerm("");
    setSelectedTag("");
  };

  const handleRegister = async (eventId) => {
    if (!userData) {
      alert("Please log in to register for events.");
      return;
    }

    const event = events.find((e) => e.id === eventId);
    if (event.registeredCount >= event.maxAttendees) {
      alert("This event is full. Registration is not available.");
      return;
    }

    try {
      const registrationData = {
        eventId: eventId,
        userId: userData.id,
        userName: userData.name,
        userEmail: userData.email,
        studentId: userData.studentId,
        registrationDate: new Date().toISOString(),
        attended: false,
      };

      const docRef = await addDoc(
        collection(db, "registrations"),
        registrationData
      );
      await updateDoc(doc(db, "events", eventId), {
        registeredCount: increment(1),
      });

      // Update local state
      setUserRegistrations((prev) => ({
        ...prev,
        [eventId]: docRef.id,
      }));

      // Update events list
      setEvents((prev) =>
        prev.map((event) =>
          event.id === eventId
            ? { ...event, registeredCount: event.registeredCount + 1 }
            : event
        )
      );

      alert(" Successfully registered for the event!");
    } catch (error) {
      console.error("Error registering for event:", error);
      alert(" Failed to register for the event.");
    }
  };

  const handleUnregister = async (eventId) => {
    const registrationId = userRegistrations[eventId];
    if (!registrationId) return;

    if (window.confirm("Are you sure you want to unregister from this event?")) {
      try {
        await deleteDoc(doc(db, "registrations", registrationId));
        await updateDoc(doc(db, "events", eventId), {
          registeredCount: increment(-1),
        });

        // Update local state
        setUserRegistrations((prev) => {
          const updated = { ...prev };
          delete updated[eventId];
          return updated;
        });

        // Update events list
        setEvents((prev) =>
          prev.map((event) =>
            event.id === eventId
              ? { ...event, registeredCount: event.registeredCount - 1 }
              : event
          )
        );

        alert(" Successfully unregistered from the event.");
      } catch (error) {
        console.error("Error unregistering from event:", error);
        alert(" Failed to unregister from the event.");
      }
    }
  };

  return (
    <main className="flex-fill">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <div className="bg-primary bordersm px-3 py-4 d-flex align-items-center justify-content-between mb-4">
              <div>
                <h3 className="text-white">Campus Events</h3>
                <span className="text-white">Discover and join amazing events happening around campus</span>
              </div>
              <div>
                <Link to="/Create" className="btn bg-white rounded-lg font-weight-bold">
                  <i className="fa-regular fa-plus mr-1"></i> Create Events
                </Link>
              </div>
            </div>
          </div>

          {/* Filter Section */}
          <div className="col-md-12 mb-4">
            <div className="card">
              <div className="card-body">
                <h5 className="card-title mb-3">
                  <i className="fa-regular fa-filter mr-2"></i>
                  Filter Events
                </h5>
                <div className="row">
                  <div className="col-md-6 mb-3">
                    <label htmlFor="searchInput" className="form-label">Search by name, description, or location</label>
                    <input
                      type="text"
                      id="searchInput"
                      className="form-control"
                      placeholder="Enter search term..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                  </div>
                  <div className="col-md-4 mb-3">
                    <label htmlFor="tagSelect" className="form-label">Filter by tag</label>
                    <select
                      id="tagSelect"
                      className="form-control"
                      value={selectedTag}
                      onChange={(e) => setSelectedTag(e.target.value)}
                    >
                      <option value="">All tags</option>
                      {allTags.map((tag, index) => (
                        <option key={index} value={tag}>#{tag}</option>
                      ))}
                    </select>
                  </div>
                  <div className="col-md-2 mb-3 d-flex align-items-end">
                    <button
                      className="btn btn-outline-secondary w-100"
                      onClick={clearFilters}
                      disabled={!searchTerm && !selectedTag}
                    >
                      Clear Filters
                    </button>
                  </div>
                </div>
                <div className="d-flex justify-content-between align-items-center">
                  <small className="text-muted">
                    Showing {filteredEvents.length} of {events.length} events
                  </small>
                  {(searchTerm || selectedTag) && (
                    <div className="d-flex flex-wrap">
                      {searchTerm && (
                        <span className="badge bg-primary me-2 mb-1">
                          Search: "{searchTerm}"
                          <button
                            className="btn-close btn-close-white ms-1"
                            style={{ fontSize: '0.7em' }}
                            onClick={() => setSearchTerm('')}
                          ></button>
                        </span>
                      )}
                      {selectedTag && (
                        <span className="badge bg-success me-2 mb-1">
                          Tag: #{selectedTag}
                          <button
                            className="btn-close btn-close-white ms-1"
                            style={{ fontSize: '0.7em' }}
                            onClick={() => setSelectedTag('')}
                          ></button>
                        </span>
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>

          {/* Events List */}
          {filteredEvents.length === 0 ? (
            <div className="col-md-12">
              <div className="text-center py-5">
                <i className="fa-regular fa-calendar-xmark fa-3x text-muted mb-3"></i>
                <h4 className="text-muted">No events found</h4>
                <p className="text-muted">
                  {events.length === 0 
                    ? "No events are currently available." 
                    : "Try adjusting your search criteria or clearing the filters."
                  }
                </p>
                {(searchTerm || selectedTag) && (
                  <button className="btn btn-primary" onClick={clearFilters}>
                    Clear Filters
                  </button>
                )}
              </div>
            </div>
          ) : (
            /*  Loop over filtered events */
            filteredEvents.map((event) => (
            <div key={event.id} className="col-md-4 mb-3 phone-mg">
              <div className="bordersm bg-white border shadow-sm p-3">
                <div className="d-flex align-items-center justify-content-between mb-3">
                  <span className="text-primary">{event.category || "Uncategorized"}</span>
                  <span className="text-muted">{event.maxAttendees - event.registeredCount} spots left</span>
                </div>

                <div className="mb-3">
                  <h5>{event.title}</h5>
                  <p className="text-muted">{event.description}</p>

                  <div className="d-flex align-items-center flex-wrap mb-3">
                    {(event.tags || []).map((tag, i) => (
                      <span key={i} className="d-inline-block px-2 py-1 small bg-light text-secondary rounded mr-1 mt-1">
                        #{tag}
                      </span>
                    ))}
                  </div>

                  <div className="d-flex flex-column text-muted small mb-2">
                    <div style={{ fontSize: 16 }} className="d-flex align-items-center mb-1">
                      <i className="fa-light fa-calendar mr-2"></i>
                      <span>{event.date}</span>
                    </div>
                    <div style={{ fontSize: 16 }} className="d-flex align-items-center mb-1">
                      <i className="fa-regular fa-clock mr-2"></i>
                      <span>{event.startTime} - {event.endTime}</span>
                    </div>
                    <div style={{ fontSize: 16 }} className="d-flex align-items-center mb-1">
                      <i className="fa-regular fa-location-dot mr-2"></i>
                      <span>{event.location}</span>
                    </div>
                    <div style={{ fontSize: 16 }} className="d-flex align-items-center">
                      <i className="fa-light fa-users mr-1"></i>
                      <span>{event.registeredCount}/{event.maxAttendees}</span>
                    </div>
                  </div>
                </div>

                <div className="d-flex flex-wrap align-items-center justify-content-between">
                  <Link to={`/Details/${event.id}`} style={{ backgroundColor: "#f3f4f6" }} className="btn rounded-lg px-4">
                    View Details
                  </Link>
                  {userRegistrations[event.id] ? (
                    <button 
                      className="btn btn-danger rounded-lg px-4"
                      onClick={() => handleUnregister(event.id)}
                    >
                      Unregister
                    </button>
                  ) : (
                    <button 
                      className="btn btn-success rounded-lg px-4"
                      onClick={() => handleRegister(event.id)}
                      disabled={event.registeredCount >= event.maxAttendees}
                    >
                      {event.registeredCount >= event.maxAttendees ? 'Full' : 'Register'}
                    </button>
                  )}
                </div>
              </div>
            </div>
          ))
          )}

        </div>
      </div>
    </main>
  );
}

export default Events;