import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import useCurrentUser from "../Hooks/useCurrentUser";
import { db } from "../firebase";
import { collection, query, where, getDocs, orderBy, limit, doc, updateDoc } from "firebase/firestore";

function Profile() {
    const { userData, setUserData } = useCurrentUser();
    const [registeredEventsCount, setRegisteredEventsCount] = useState(0);
    const [attendedEventsCount, setAttendedEventsCount] = useState(0);
    const [createdEventsCount, setCreatedEventsCount] = useState(0);
    const [attendanceRate, setAttendanceRate] = useState(0);
    const [recentEvents, setRecentEvents] = useState([]);
    const [loading, setLoading] = useState(true);
    const [isEditing, setIsEditing] = useState(false);
    const [editForm, setEditForm] = useState({
        name: '',
        bio: '',
        studentId: ''
    });

    useEffect(() => {
        const fetchProfileData = async () => {
            if (!userData) {
                setLoading(false);
                return;
            }

            // Initialize edit form with current user data
            setEditForm({
                name: userData.name || '',
                bio: userData.bio || '',
                studentId: userData.studentId || ''
            });

            try {
                // Fetch registered events
                const registeredQuery = query(
                    collection(db, "registrations"),
                    where("userId", "==", userData.id)
                );
                const registeredSnapshot = await getDocs(registeredQuery);
                setRegisteredEventsCount(registeredSnapshot.size);

                // Fetch attended events
                const attendedQuery = query(
                    collection(db, "registrations"),
                    where("userId", "==", userData.id),
                    where("attended", "==", true)
                );
                const attendedSnapshot = await getDocs(attendedQuery);
                setAttendedEventsCount(attendedSnapshot.size);

                // Calculate attendance rate
                if (registeredSnapshot.size > 0) {
                    setAttendanceRate(((attendedSnapshot.size / registeredSnapshot.size) * 100).toFixed(2));
                } else {
                    setAttendanceRate(0);
                }

                // Fetch created events (if organizer)
                if (userData.role === "organizer") {
                    const createdEventsQuery = query(
                        collection(db, "events"),
                        where("organizerId", "==", userData.id)
                    );
                    const createdEventsSnapshot = await getDocs(createdEventsQuery);
                    setCreatedEventsCount(createdEventsSnapshot.size);
                }

                // Fetch recent events (registered or created)
                const recentRegisteredQuery = query(
                    collection(db, "registrations"),
                    where("userId", "==", userData.id),
                    orderBy("registrationDate", "desc"), // Assuming a registrationDate field
                    limit(3)
                );
                const recentRegisteredSnapshot = await getDocs(recentRegisteredQuery);
                const recentRegisteredEventIds = recentRegisteredSnapshot.docs.map(doc => doc.data().eventId);

                let fetchedRecentEvents = [];
                if (recentRegisteredEventIds.length > 0) {
                    const eventsQuery = query(
                        collection(db, "events"),
                        where("__name__", "in", recentRegisteredEventIds)
                    );
                    const eventsSnapshot = await getDocs(eventsQuery);
                    fetchedRecentEvents = eventsSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
                }

                if (userData.role === "organizer") {
                    const recentCreatedQuery = query(
                        collection(db, "events"),
                        where("organizerId", "==", userData.id),
                        orderBy("createdAt", "desc"),
                        limit(3)
                    );
                    const recentCreatedSnapshot = await getDocs(recentCreatedQuery);
                    const createdEvents = recentCreatedSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
                    // Combine and deduplicate events
                    const combinedEvents = [...fetchedRecentEvents, ...createdEvents];
                    const uniqueEvents = Array.from(new Map(combinedEvents.map(item => [item.id, item])).values());
                    setRecentEvents(uniqueEvents.sort((a, b) => new Date(b.date) - new Date(a.date)).slice(0, 3));
                } else {
                    setRecentEvents(fetchedRecentEvents.sort((a, b) => new Date(b.date) - new Date(a.date)).slice(0, 3));
                }

            } catch (error) {
                console.error("Error fetching profile data:", error);
            } finally {
                setLoading(false);
            }
        };

        fetchProfileData();
    }, [userData]);

    const handleEditToggle = () => {
        if (isEditing) {
            // Reset form to original values when canceling
            setEditForm({
                name: userData.name || '',
                bio: userData.bio || '',
                studentId: userData.studentId || ''
            });
        }
        setIsEditing(!isEditing);
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setEditForm(prev => ({
            ...prev,
            [name]: value
        }));
    };

    const handleSaveProfile = async () => {
        try {
            const userRef = doc(db, 'users', userData.id);
            await updateDoc(userRef, {
                name: editForm.name,
                bio: editForm.bio,
                studentId: editForm.studentId
            });

            // Update local user data
            setUserData(prev => ({
                ...prev,
                name: editForm.name,
                bio: editForm.bio,
                studentId: editForm.studentId
            }));

            setIsEditing(false);
            alert('Profile updated successfully!');
        } catch (error) {
            console.error('Error updating profile:', error);
            alert('Failed to update profile. Please try again.');
        }
    };

    if (loading) {
        return <div className="text-center py-5">Loading profile data...</div>;
    }

    return (
        <main className="flex-fill">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 mb-3">
                        <div className="d-lg-flex d-block align-items-center justify-content-between mb-4">
                            <div className="mb-lg-0 mb-3">
                                <h3 className="font-weight-bold">Profile</h3>
                                <span className="text-muted">View and Edit data your info
                                </span>
                            </div>
                            <div>
                                {!isEditing ? (
                                    <button onClick={handleEditToggle} className="btn bordfm text-white px-4 btn-primary">
                                        <i className="fa-light fa-pen mr-1"></i> Edit Profile
                                    </button>
                                ) : (
                                    <div>
                                        <button onClick={handleSaveProfile} className="btn btn-success mr-2">
                                            <i className="fa-light fa-check mr-1"></i> Save
                                        </button>
                                        <button onClick={handleEditToggle} className="btn btn-secondary">
                                            <i className="fa-light fa-times mr-1"></i> Cancel
                                        </button>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>

                    <div className="col-md-8">
                        <div className="border bordersm bg-white p-3 d-flex mb-4">
                            <div className="mr-0 mr-md-4">
                                <div style={{ backgroundColor: "#e5e7eb" }} className="p-1 rounded-circle">
                                    <button className="btn btn-primary profile-circle rounded-circle">{userData?.name ? userData.name.substring(0, 2).toUpperCase() : "GU"}</button>
                                </div>
                            </div>

                            <div>
                                {!isEditing ? (
                                    <>
                                        <h3>{userData?.name || "Guest"}</h3>
                                        <p className="text-muted mb-1"> <i className="fa-light fa-envelope mr-1"></i> {userData?.email || "N/A"}</p>
                                        <p className="text-muted mb-1"> <i className="fa-light fa-shield mr-1"></i> {userData?.role || "N/A"} </p>
                                        <p className="text-muted mb-1"> <i className="fa-light fa-user mr-1"></i> {userData?.studentId || "N/A"} </p>
                                        <p className="text-muted mb-1"> Bio: {userData?.bio || "No bio available."}</p>
                                    </>
                                ) : (
                                    <div>
                                        <div className="form-group mb-3">
                                            <label htmlFor="name">Full Name</label>
                                            <input
                                                type="text"
                                                id="name"
                                                name="name"
                                                className="form-control"
                                                value={editForm.name}
                                                onChange={handleInputChange}
                                            />
                                        </div>
                                        <div className="form-group mb-3">
                                            <label htmlFor="studentId">Student ID</label>
                                            <input
                                                type="text"
                                                id="studentId"
                                                name="studentId"
                                                className="form-control"
                                                value={editForm.studentId}
                                                onChange={handleInputChange}
                                            />
                                        </div>
                                        <div className="form-group mb-3">
                                            <label htmlFor="bio">Bio</label>
                                            <textarea
                                                id="bio"
                                                name="bio"
                                                className="form-control"
                                                rows="3"
                                                value={editForm.bio}
                                                onChange={handleInputChange}
                                                placeholder="Tell us about yourself..."
                                            />
                                        </div>
                                        <p className="text-muted mb-1"> <i className="fa-light fa-envelope mr-1"></i> {userData?.email || "N/A"} (Cannot be changed)</p>
                                        <p className="text-muted mb-1"> <i className="fa-light fa-shield mr-1"></i> {userData?.role || "N/A"} (Cannot be changed)</p>
                                    </div>
                                )}
                            </div>
                        </div>

                        <div className="border bordersm bg-white p-4 d-flex flex-column justify-content-center mb-4">
                            <h4 className="mb-3">Your Activities</h4>
                            <div className="d-flex align-items-center justify-content-between">
                                <div style={{ backgroundColor: "#eff6ff", color: "#1d4ed8", width: "23%" }}
                                    className="p-3 border-0 bordfm text-center">
                                    <i className="fa-light fa-calendar fa-xl mb-4"></i>
                                    <h3>{registeredEventsCount}</h3>
                                    <span>Registered<br />events</span>
                                </div>

                                <div style={{ backgroundColor: "#f0fdf4", color: "#15803d", width: "23%" }}
                                    className="p-3 border-0 bordfm text-center">
                                    <i className="fa-light fa-medal fa-xl mb-4"></i>
                                    <h3>{attendedEventsCount}</h3>
                                    <span>Events<br />attended</span>
                                </div>

                                {userData?.role === "organizer" && (
                                    <div style={{ backgroundColor: "#faf5ff", color: "#7e22ce", width: "23%" }}
                                        className="p-3 border-0 bordfm text-center">
                                        <i className="fa-light fa-plus fa-xl mb-4"></i>
                                        <h3>{createdEventsCount}</h3>
                                        <span>Events<br />Created</span>
                                    </div>
                                )}

                                <div style={{ backgroundColor: "#fefce8", color: "#a16207", width: "23%" }}
                                    className="p-3 border-0 bordfm text-center">
                                    <i className="fa-light fa-gear fa-xl mb-4"></i>
                                    <h3>{attendanceRate}%</h3>
                                    <span>Attendance<br />Rate</span>
                                </div>
                            </div>
                        </div>

                        <div className="border bordersm bg-white p-4 d-flex flex-column justify-content-center">
                            <h4 className="mb-3">Recent Events</h4>
                            {recentEvents.length === 0 ? (
                                <p>No recent events to display.</p>
                            ) : (
                                recentEvents.map(event => (
                                    <div key={event.id} className="mb-3 border py-4 px-3 bordersm">
                                        <div className="d-flex justify-content-between">
                                            <div>
                                                <h3 className="h6 font-weight-bold text-dark mb-2">{event.title}</h3>
                                                <div className="d-flex text-muted small mb-2">
                                                    <div className="d-flex align-items-center mr-3">
                                                        <i className="fa-light fa-calendar mr-1"></i>
                                                        <span>{event.date}</span>
                                                    </div>
                                                    <div className="d-flex align-items-center mr-3">
                                                        <i className="fa-regular fa-clock mr-1"></i>
                                                        <span>{event.time}</span>
                                                    </div>
                                                    <div className="d-flex align-items-center">
                                                        <i className="fa-light fa-users mr-1"></i>
                                                        <span>{event.registeredCount || 0}/{event.maxAttendees}</span>
                                                    </div>
                                                </div>
                                                <div className="d-flex align-items-center">
                                                    {event.tags && event.tags.map((tag, index) => (
                                                        <span key={index} className="d-inline-block px-2 py-1 small bg-light text-secondary rounded mr-1">#{tag}</span>
                                                    ))}
                                                </div>
                                            </div>
                                            <div>
                                                {/* You might want to add a link to event details or registration status here */}
                                                <Link to={`/Details/${event.id}`} className="btn btn-primary rounded-pill">View Details</Link>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            )}
                        </div>
                    </div>

                    <div className="col-md-4">
                        <div className="border bordersm bg-white p-3">
                            <h4 className="mb-3 text-center">Achievements</h4>
                            {/* Add dynamic achievements here */}
                            <p className="text-muted text-center">No achievements yet.</p>
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}
export default Profile;

