import { useEffect, useState } from "react";
import { db } from "../firebase";
import { collection, getDocs, query, where } from "firebase/firestore";

function Archived() {
  const [archivedEvents, setArchivedEvents] = useState([]);
  const [totalArchivedEvents, setTotalArchivedEvents] = useState(0);
  const [totalConfirmedAttendees, setTotalConfirmedAttendees] = useState(0);
  const [averageAttendanceRate, setAverageAttendanceRate] = useState(0);

  useEffect(() => {
    const fetchArchivedEvents = async () => {
      try {
        const eventsRef = collection(db, "events");
        const q = query(eventsRef, where("archived", "==", true));
        const querySnapshot = await getDocs(q);
        const eventsData = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setArchivedEvents(eventsData);
        setTotalArchivedEvents(eventsData.length);

        let confirmedAttendeesCount = 0;
        let totalPossibleAttendees = 0;

        for (const event of eventsData) {
          const registrationsQuery = query(
            collection(db, "registrations"),
            where("eventId", "==", event.id)
          );
          const registrationsSnapshot = await getDocs(registrationsQuery);
          const registrationsData = registrationsSnapshot.docs.map(doc => doc.data());

          confirmedAttendeesCount += registrationsData.filter(reg => reg.attended).length;
          totalPossibleAttendees += registrationsData.length;
        }

        setTotalConfirmedAttendees(confirmedAttendeesCount);

        if (totalPossibleAttendees > 0) {
          setAverageAttendanceRate(((confirmedAttendeesCount / totalPossibleAttendees) * 100).toFixed(2));
        } else {
          setAverageAttendanceRate(0);
        }

      } catch (error) {
        console.error("Error fetching archived events:", error);
      }
    };

    fetchArchivedEvents();
  }, []);

  const exportCSV = async () => {
  let csvContent = "data:text/csv;charset=utf-8,";
  csvContent += "Event Name,Event Date,Event Location,Student Name,Student Email,Attended\n";

  for (const event of archivedEvents) {
    const registrationsQuery = query(
      collection(db, "registrations"),
      where("eventId", "==", event.id)
    );
    const registrationsSnapshot = await getDocs(registrationsQuery);
    const registrationsData = registrationsSnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));

    if (registrationsData.length === 0) {
      csvContent += `"${event.title}","${event.date}","${event.location}",No registrations,,\n`;
    } else {
      for (const reg of registrationsData) {
        const studentName = reg.userName || "N/A";
        const studentEmail = reg.userEmail || "N/A";

        csvContent += `"${event.title}","${event.date}","${event.location}","${studentName}","${studentEmail}",${reg.attended ? 'Yes' : 'No'}\n`;
      }
    }
  }

  const encodedUri = encodeURI(csvContent);
  const link = document.createElement("a");
  link.setAttribute("href", encodedUri);
  link.setAttribute("download", "archived_events_students.csv");
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
};


  const exportJSON = () => {
    const jsonContent = JSON.stringify(archivedEvents, null, 2);
    const blob = new Blob([jsonContent], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const link = document.createElement("a");
    link.setAttribute("href", url);
    link.setAttribute("download", "archived_events.json");
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="flex-fill">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mb-4">
            <div className="d-lg-flex d-block align-items-center justify-content-between mb-4">
              <div className="mb-lg-0 mb-3">
                <h3 className="font-weight-bold">Past Events</h3>
                <span className="text-muted">View and export data from completed events with confirmed attendees</span>
              </div>
              <div>
                <button className="btn btn-success bordfm mr-2" onClick={exportCSV}>
                  <i className="fa-regular fa-arrow-down-to-line mr-1"></i> Export CSV
                </button>
                <button style={{ backgroundColor: "#9333ea" }} className="btn bordfm text-white" onClick={exportJSON}>
                  <i className="fa-light fa-file"></i> Export JSON
                </button>
              </div>
            </div>
          </div>

          <div className="col-md-4 mb-4">
            <div className="bg-white bordersm shadow-sm border p-4">
              <div className="d-flex align-items-center">
                <div className="p-2 mr-3 bordfm" style={{ backgroundColor: "#dbeafe" }}>
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"
                    stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                    className="lucide lucide-archive text-primary">
                    <rect width="20" height="5" x="2" y="3" rx="1"></rect>
                    <path d="M4 8v11a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8"></path>
                    <path d="M10 12h4"></path>
                  </svg>
                </div>
                <div>
                  <p className="text-muted small mb-1">Total Archived Events</p>
                  <p className="h4 font-weight-bold text-dark mb-0">{totalArchivedEvents}</p>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-4 mb-4">
            <div className="bg-white bordersm shadow-sm border p-4">
              <div className="d-flex align-items-center">
                <div className="p-2 mr-3 bordfm" style={{ backgroundColor: "#d1fae5" }}>
                  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"
                    stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                    className="lucide lucide-users text-success">
                    <path d="M16 21v-2a4 4 0 0 0-4-4H6a4 4 0 0 0-4 4v2"></path>
                    <circle cx="9" cy="7" r="4"></circle>
                    <path d="M22 21v-2a4 4 0 0 0-3-3.87"></path>
                    <path d="M16 3.13a4 4 0 0 1 0 7.75"></path>
                  </svg>
                </div>
                <div>
                  <p className="text-muted small mb-1">Total Confirmed Attendees</p>
                  <p className="h4 font-weight-bold text-dark mb-0">{totalConfirmedAttendees}</p>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-4 mb-4">
            <div className="bg-white bordersm shadow-sm border p-4">
              <div className="d-flex align-items-center">
                <div className="p-2 mr-3 bordfm" style={{ backgroundColor: "#ede9fe" }}>
                  <svg style={{ color: "#9333ea" }} xmlns="http://www.w3.org/2000/svg" width="24" height="24"
                    fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                    className="lucide lucide-calendar text-purple">
                    <rect width="18" height="18" x="3" y="4" rx="2" ry="2"></rect>
                    <line x1="16" x2="16" y1="2" y2="6"></line>
                    <line x1="8" x2="8" y1="2" y2="6"></line>
                    <line x1="3" x2="21" y1="10" y2="10"></line>
                  </svg>
                </div>
                <div>
                  <p className="text-muted small mb-1">Average Attendance Rate</p>
                  <p className="h4 font-weight-bold text-dark mb-0">{averageAttendanceRate}%</p>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-12">
            {archivedEvents.length === 0 ? (
              <p>No archived events found.</p>
            ) : (
              archivedEvents.map((event) => (
                <div key={event.id} className=" bg-white bordersm shadow-sm border p-4 mb-4 d-flex flex-column flex-lg-row justify-content-between align-items-start align-items-lg-center">
                  <div className="flex-grow-1">
                    <div className="d-flex align-items-start mb-3">
                      <div className="p-2 bg-light rounded mr-3">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"
                          stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                          className="lucide lucide-archive text-muted">
                          <rect width="20" height="5" x="2" y="3" rx="1"></rect>
                          <path d="M4 8v11a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V8"></path>
                          <path d="M10 12h4"></path>
                        </svg>
                      </div>
                      <div className="flex-grow-1">
                        <h3 className="h5 font-weight-bold text-dark mb-2">{event.title}</h3>
                        <div className="row text-muted small mb-2">
                          <div className="col-sm-6 col-lg-3 mb-2 d-flex align-items-center">
                            <i className="fa-light fa-calendar mr-2"></i>
                            <span>{event.date}</span>
                          </div>
                          <div className="col-sm-6 col-lg-3 mb-2 d-flex align-items-center">
                            <i className="fa-regular fa-location-dot mr-2"></i>
                            <span>{event.location}</span>
                          </div>
                          <div className="col-sm-6 col-lg-3 mb-2 d-flex align-items-center">
                            <i className="fa-light fa-users mr-2"></i>
                            <span>{event.attendedCount || 0} attended</span>
                          </div>
                          <div className="col-sm-6 col-lg-3 mb-2 d-flex align-items-center">
                            <i className="fa-light fa-clock mr-2"></i>
                            <span>Archived {event.archiveDate}</span>
                          </div>
                        </div>
                        <div className="mt-2 d-flex flex-wrap align-items-center">
                          <span className="badge p-2 rounded-pill badge-success mr-3">{event.registeredCount > 0 ? ((event.attendedCount || 0) / event.registeredCount * 100).toFixed(2) : 0}% attendance rate</span>
                          <span className="text-muted small">{event.registeredCount || 0} registered  {event.attendedCount || 0} attended</span>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="d-flex flex-wrap gap-2">
                    <button className="btn btn-success bordfm d-flex align-items-center mr-2" onClick={exportCSV}>
                      <i className="fa-regular fa-arrow-down-to-line mr-2"></i> CSV
                    </button>
                    <button style={{ backgroundColor: "#9333ea" }} className="btn text-white bordfm d-flex align-items-center" onClick={exportJSON}>
                      <i className="fa-light fa-file-text mr-2"></i> JSON
                    </button>
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
export default Archived;

