import React, { useEffect, useState } from 'react';
import QRCode from 'qrcode';

function QRCodeGenerator({ eventId, userId, userName, registrationId }) {
  const [qrCodeUrl, setQrCodeUrl] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const generateQRCode = async () => {
      try {
        // Create QR code data with event and user information
        const qrData = {
          eventId,
          userId,
          userName,
          registrationId,
          timestamp: new Date().toISOString()
        };

        // Generate QR code as data URL
        const qrCodeDataUrl = await QRCode.toDataURL(JSON.stringify(qrData), {
          width: 200,
          margin: 2,
          color: {
            dark: '#000000',
            light: '#FFFFFF'
          }
        });

        setQrCodeUrl(qrCodeDataUrl);
      } catch (error) {
        console.error('Error generating QR code:', error);
      } finally {
        setLoading(false);
      }
    };

    if (eventId && userId) {
      generateQRCode();
    }
  }, [eventId, userId, userName, registrationId]);

  if (loading) {
    return (
      <div className="text-center py-3">
        <div className="spinner-border text-primary" role="status">
          <span className="sr-only">Generating QR Code...</span>
        </div>
        <p className="mt-2 text-muted">Generating your QR code...</p>
      </div>
    );
  }

  if (!qrCodeUrl) {
    return (
      <div className="text-center py-3">
        <p className="text-danger">Failed to generate QR code</p>
      </div>
    );
  }

  return (
    <div className="text-center">
      <div className="mb-3">
        <img 
          src={qrCodeUrl} 
          alt="Event Registration QR Code" 
          className="border rounded"
          style={{ maxWidth: '200px', height: 'auto' }}
        />
      </div>
      <div className="small text-muted">
        <p className="mb-1"><strong>Event QR Code</strong></p>
        <p className="mb-1">Show this to the organizer for attendance</p>
        <p className="mb-0">User: {userName}</p>
      </div>
    </div>
  );
}

export default QRCodeGenerator;

