import { createRoot } from "react-dom/client";
import {createBrowserRouter,RouterProvider} from "react-router-dom";
import App from "./App";
import { CartProvider } from "react-use-cart";
import ScrollToTop from "react-scroll-to-top";
import Allevents from "./Events/Allevents";
import Allcreate from "./CreateEvents/Allcreate";
import Allarchived from "./ArchievedEvents/Allarchived";
import Allprofile from "./Profile/Allprofile";
import Allsignup from "./Registeration/Allsignup";
import Alllogin from "./Login/Alllogin";
import Alldetails from "./Details/Alldetails";
import ProtectedRoute from "./Auth/ProtectedRoute";

const router = createBrowserRouter([
  {
    path: "/login",
    element: <Alllogin/>,
  },
  {
    path: "/register",
    element: <Allsignup/>,
  },
  {
    path: "/",
    element: <ProtectedRoute><App/></ProtectedRoute>,
  },
  {
    path: "/Events",
    element: <ProtectedRoute><Allevents/></ProtectedRoute>,
  },
  {
    path: "/Create",
    element: <ProtectedRoute requiredRole="organizer"><Allcreate/></ProtectedRoute>,
  },
  {
    path: "/Archived",
    element: <ProtectedRoute><Allarchived/></ProtectedRoute>,
  },
  {
    path: "/Profile",
    element: <ProtectedRoute><Allprofile/></ProtectedRoute>,
  },
  {
    path: "/Details/:eventId",
    element: <ProtectedRoute><Alldetails/></ProtectedRoute>,
  },
]);

createRoot(document.getElementById("root")).render(
  <CartProvider>
    <RouterProvider router={router} />
    <ScrollToTop
    smooth
    viewBox="0 0 448 512"
    svgPath="M201.4 137.4c12.5-12.5 32.8-12.5 45.3 0l160 160c12.5 12.5 12.5 32.8 0 45.3s-32.8 12.5-45.3 0L224 205.3 86.6 342.6c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3l160-160z"
    />
  </CartProvider>
);