import React, { useEffect, useRef, useState } from 'react';
import QrScanner from 'qr-scanner';
import { db } from '../firebase';
import { doc, updateDoc, getDoc, increment } from 'firebase/firestore';

function QRScanner({ eventId, onScanSuccess, onScanError }) {
  const videoRef = useRef(null);
  const [scanner, setScanner] = useState(null);
  const [isScanning, setIsScanning] = useState(false);
  const [scanResult, setScanResult] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    return () => {
      // Cleanup scanner on unmount
      if (scanner) {
        scanner.stop();
        scanner.destroy();
      }
    };
  }, [scanner]);

  const startScanning = async () => {
    try {
      setError(null);
      setIsScanning(true);

      const qrScanner = new QrScanner(
        videoRef.current,
        async (result) => {
          try {
            // Parse QR code data
            const qrData = JSON.parse(result.data);

            // Validate QR code data
            if (!qrData.eventId || !qrData.userId || !qrData.registrationId) {
              throw new Error('Invalid QR code format');
            }



            // Update attendance in Firebase
            await markAttendance(qrData.registrationId, qrData.userId, qrData.userName);

            setScanResult({
              success: true,
              message: `Successfully marked ${qrData.userName} as attended!`,
              userData: qrData
            });

            if (onScanSuccess) {
              onScanSuccess(qrData);
            }

            // Stop scanning after successful scan
            qrScanner.stop();
            setIsScanning(false);

          } catch (error) {
            console.error('Error processing QR code:', error);
            setScanResult({
              success: false,
              message: error.message || 'Failed to process QR code'
            });

            if (onScanError) {
              onScanError(error);
            }
          }
        },
        {
          returnDetailedScanResult: true,
          highlightScanRegion: true,
          highlightCodeOutline: true,
        }
      );

      setScanner(qrScanner);
      await qrScanner.start();

    } catch (error) {
      console.error('Error starting QR scanner:', error);
      setError('Failed to start camera. Please check camera permissions.');
      setIsScanning(false);
    }
  };

  const stopScanning = () => {
    if (scanner) {
      scanner.stop();
      setIsScanning(false);
    }
  };

  const markAttendance = async (registrationId, userId, userName) => {
    try {
      // Update the registration document to mark as attended
      const registrationRef = doc(db, 'registrations', registrationId);

      // First check if the registration exists
      const registrationDoc = await getDoc(registrationRef);
      if (!registrationDoc.exists()) {
        throw new Error('Registration not found');
      }

      // Update attendance status
      await updateDoc(registrationRef, {
        attended: true,
        attendanceTime: new Date().toISOString(),
        attendanceMarkedBy: 'organizer' // In a real app, this would be the current organizer's ID
      });

      const eventRef = doc(db, 'events', eventId);

      await updateDoc(eventRef, {
        attendedCount: increment(1),
      });

      console.log(`Marked attendance for ${userName} (${userId})`);
    } catch (error) {
      console.error('Error marking attendance:', error);
      throw new Error('Failed to mark attendance in database');
    }
  };

  const clearResult = () => {
    setScanResult(null);
    setError(null);
  };

  return (
    <div className="qr-scanner-container">
      <div className="text-center mb-3">
        {!isScanning ? (
          <button
            className="btn btn-success btn-lg"
            onClick={startScanning}
          >
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="me-2">
              <path d="M3 7V5a2 2 0 0 1 2-2h2"></path>
              <path d="M17 3h2a2 2 0 0 1 2 2v2"></path>
              <path d="M21 17v2a2 2 0 0 1-2 2h-2"></path>
              <path d="M7 21H5a2 2 0 0 1-2-2v-2"></path>
            </svg>
            Start QR Scanner
          </button>
        ) : (
          <button
            className="btn btn-danger btn-lg"
            onClick={stopScanning}
          >
            <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="me-2">
              <rect width="6" height="6" x="9" y="9"></rect>
            </svg>
            Stop Scanner
          </button>
        )}
      </div>

      {/* Video element for camera feed */}
      <div className="scanner-video-container mb-3" style={{ display: isScanning ? 'block' : 'none' }}>
        <video
          ref={videoRef}
          style={{
            width: '100%',
            maxWidth: '400px',
            height: 'auto',
            border: '2px solid #007bff',
            borderRadius: '8px'
          }}
        />
        <p className="text-center mt-2 text-muted">
          Point the camera at a QR code to scan
        </p>
      </div>

      {/* Scan results */}
      {scanResult && (
        <div className={`alert ${scanResult.success ? 'alert-success' : 'alert-danger'} alert-dismissible`}>
          <div className="d-flex justify-content-between align-items-center">
            <div>
              <strong>{scanResult.success ? ' Success!' : ' Error'}</strong>
              <p className="mb-0">{scanResult.message}</p>
            </div>
            <button
              type="button"
              className="btn-close"
              onClick={clearResult}
              aria-label="Close"
            ></button>
          </div>
        </div>
      )}

      {/* Error messages */}
      {error && (
        <div className="alert alert-danger alert-dismissible">
          <div className="d-flex justify-content-between align-items-center">
            <div>
              <strong> Camera Error</strong>
              <p className="mb-0">{error}</p>
            </div>
            <button
              type="button"
              className="btn-close"
              onClick={() => setError(null)}
              aria-label="Close"
            ></button>
          </div>
        </div>
      )}

      {/* Instructions */}
      <div className="card mt-3">
        <div className="card-body">
          <h6 className="card-title">How to use QR Scanner:</h6>
          <ul className="small text-muted mb-0">
            <li>Click "Start QR Scanner" to activate the camera</li>
            <li>Point the camera at the attendee's QR code</li>
            <li>The system will automatically mark attendance when scanned</li>
            <li>Make sure the QR code is well-lit and clearly visible</li>
          </ul>
        </div>
      </div>
    </div>
  );
}

export default QRScanner;

