import { Link } from 'react-router-dom'
import { useState } from 'react'
import { db } from '../firebase'
import { collection, addDoc } from 'firebase/firestore'
import useCurrentUser from '../Hooks/useCurrentUser'

function Create() {
    const { userData } = useCurrentUser()
    const [title, setTitle] = useState('')
    const [description, setDescription] = useState('')
    const [date, setDate] = useState('')
    const [startTime, setStartTime] = useState('')
    const [endTime, setEndTime] = useState('')
    const [location, setLocation] = useState('')
    const [category, setCategory] = useState('')
    const [maxAttendees, setMaxAttendees] = useState(20)
    const [tags, setTags] = useState('')

    const handleSubmit = async (e) => {
        e.preventDefault()
        console.log("Submitting form...");

        if (!userData) {
            alert(' You must be logged in to create events.');
            return;
        }

        const event = {
            title,
            description,
            date,
            startTime,
            endTime,
            location,
            category,
            maxAttendees: parseInt(maxAttendees),
            registeredCount: 0,
            attendedCount: 0,
            tags: tags.split(',').map(tag => tag.trim()),
            organizerId: userData.id,
            organizerName: userData.name,
            createdAt: new Date(),
            archived: false
        }

        try {
            console.log("Event to be saved:", event);
            console.log("DB instance:", db);
            await addDoc(collection(db, 'events'), event)
            alert(' Event created!')

            // Clear form
            setTitle('')
            setDescription('')
            setDate('')
            setStartTime('')
            setEndTime('')
            setLocation('')
            setCategory('')
            setMaxAttendees(20)
            setTags('')
        } catch (err) {
            console.error('Error creating event:', err)
            alert(' Failed to create event.')
        }
    }

    return (
        <div className="flex-fill">
            <div className="container">
                <div className="row">
                    <div className="col-md-12 d-flex align-items-center mb-4">
                        <Link to="/" className="btn p-1 border-0 rounded-lg mr-4">
                            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none"
                                stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"
                                className="lucide lucide-arrow-left w-5 h-5">
                                <path d="m12 19-7-7 7-7" />
                                <path d="M19 12H5" />
                            </svg>
                        </Link>
                        <h2>Create New Events</h2>
                    </div>

                    <div className="col-md-12">
                        <form className="border bordersm p-4 bg-white" onSubmit={handleSubmit}>
                            <div className="mb-4">
                                <label className="text-muted">Event Title *</label>
                                <input type="text" className="form-control py-4 bordfm"
                                    placeholder="Enter Event Title"
                                    value={title}
                                    onChange={(e) => setTitle(e.target.value)}
                                    required />
                            </div>

                            <div className="mb-4">
                                <label className="text-muted">Description *</label>
                                <textarea rows="4" className="form-control pb-4 pt-2 bordfm"
                                    placeholder="Describe your event..."
                                    value={description}
                                    onChange={(e) => setDescription(e.target.value)}
                                    required />
                            </div>

                            <div className="d-flex align-items-center flex-wrap mb-4">
                                <div>
                                    <label>Date *</label>
                                    <input type="date" className="form-control py-4 bordfm"
                                        value={date}
                                        onChange={(e) => setDate(e.target.value)}
                                        required />
                                </div>

                                <div className="mx-0 mx-sm-5">
                                    <label>Start Time *</label>
                                    <input type="time" className="form-control py-4 bordfm"
                                        value={startTime}
                                        onChange={(e) => setStartTime(e.target.value)}
                                        required />
                                </div>

                                <div>
                                    <label>End Time *</label>
                                    <input type="time" className="form-control py-4 bordfm"
                                        value={endTime}
                                        onChange={(e) => setEndTime(e.target.value)}
                                        required />
                                </div>
                            </div>

                            <div className="mb-4">
                                <label className="text-muted">Location *</label>
                                <input type="text" className="form-control py-4 bordfm"
                                    placeholder="Enter Event Location"
                                    value={location}
                                    onChange={(e) => setLocation(e.target.value)}
                                    required />
                            </div>

                            <div className="mb-4">
                                <label className="text-muted">Category</label>
                                <input type="text" className="form-control py-4 bordfm"
                                    placeholder="Enter category"
                                    value={category}
                                    onChange={(e) => setCategory(e.target.value)} />
                            </div>

                            <div className="mb-4">
                                <label className="text-muted">Max Attendees</label>
                                <input type="number" className="form-control py-4 bordfm"
                                    value={maxAttendees}
                                    onChange={(e) => setMaxAttendees(e.target.value)} />
                            </div>

                            <div className="mb-4">
                                <label className="text-muted">Tags</label>
                                <input type="text" className="form-control py-4 bordfm"
                                    placeholder="Add tags (e.g., STEM, Networking, Free Food)..."
                                    value={tags}
                                    onChange={(e) => setTags(e.target.value)} />
                            </div>

                            <hr />

                            <div className="d-flex align-items-center justify-content-end">
                                <Link to="/" className="btn btn-danger px-5 py-2 mr-3">Cancel</Link>
                                <button type="submit" className="btn btn-primary px-5 py-2">Create Event</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Create
